.inspiring-container {
  min-height: 500px;
  width: 100vw;
  background-color: #000d;
  color: #fff;
  display: flex;
  justify-content: center;
  padding: 1rem 0rem;

  .heading {
    font-size: 1rem;
    color: rgb(125, 127, 128);
    text-align: center;
    font-weight: 900;
  }
  .quote-container {
    width: 90%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    text-align: center;
    .quote-title {
      font-size: 2rem;
      font-weight: bold;
      font-family: "Fasthand";
    }
    .quote-explanation {
      font-size: 0.8rem;
      margin-top: 1rem;
      font-style: italic;
    }
  }
  .counter-container {
    display: flex;
    justify-content: center;
    align-items: center;

    .circle-wrapper {
        // border: 1px solid white;
        height: 30px;
        display: flex;
        align-items: center;

      .circle {
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: #b6b6b6;
        margin: 0 0.5rem;
        transition: all 0.3s ease-in-out;

      }
      .active {
        width: 15px;
        height: 15px;
      }
      .disable {
        background-color: #000d;
      }
    }
  }
}
